'use strict';

import React from 'react';

import './navbar.less';
import { Router, Route, Link, IndexRoute, hashHistory, browserHistory } from 'react-router';
let menuChecked = true;

class Navbar extends React.Component {
  constructor() {
    super();

    this.state = {
      currentPath: window.location.pathname,
      showDropdownMenu: false
    }

    browserHistory.listen(location =>{
      this.state.currentPath = window.location.pathname;
      this.state.showDropdownMenu = false;
    })
    
  }

  dropdownMenuToggled(event){
    this.state.showDropdownMenu = event.target.value;
  }

  

  componentWillReceiveProps (newProps){
    this.setState({menuChecked: newProps.menuChecked});
  }


  render() {
    return (
      <div className="navbar">
        <div className="inner-bar header">
          <div className="nav-button logo">
            <div className="vertically-centered-container">
              <div className="vertically-centered-content">
                <Link to='/'><img  src="/images/da-logo-white.svg"/></Link>
              </div>
            </div>
          </div>
          <div className="nav-button">
            <div className="vertically-centered-container">
              <div className="vertically-centered-content">
                <Link className={this.state.currentPath === '/composite-parts' ? 'selected-view': ''} to='/composite-parts'><b>Composite Products</b></Link>
              </div>
            </div>
          </div>
          <div className="nav-button">
            <div className="vertically-centered-container">
              <div className="vertically-centered-content">
                <Link className={this.state.currentPath === '/kit-aircraft' ? 'selected-view': ''} to='/kit-aircraft'><b>Aircraft</b></Link>
              </div>
            </div>
          </div>
          <input className="menu-btn" checked={this.state.showDropdownMenu} onChange={this.dropdownMenuToggled}  type="checkbox" id="menu-btn" />
          <label className="menu-icon" htmlFor="menu-btn"><span className="navicon"></span></label>
          
          <ul className="menu">
            <li><Link className={this.state.currentPath === '/composite-parts' ? 'selected-view': ''} to='/composite-parts'>Composite Products</Link></li>
            <li><Link className={this.state.currentPath === '/kit-aircraft' ? 'selected-view': ''} to='/kit-aircraft'>Aircraft</Link></li>
          </ul>

        </div>
          
      </div>
    );
  }
}

Navbar.displayName = 'Navbar component';

// Uncomment properties you need
// Navbar.propTypes = {};
// Navbar.defaultProps = {};

export default Navbar;



