'use strict';

import React from 'react';
import ReactGA from 'react-ga';
import $ from 'jquery';

let panel1Seen = false;
let panel2Seen = false;
let panel3Seen = false;


class PageComponent extends React.Component {
  constructor() {
    super();

    this.slideDown = this.slideDown.bind(this);
    this.scrolledToPanel = this.scrolledToPanel.bind(this);


    $(function(){
      //slide to the top initially when the view is switched
      $('html, body').animate({
          scrollTop: $('.panel-0').offset().top
        }, 0);
    })
    
    
  }
  slideDown() {
    $('html, body').animate({
        scrollTop: $('.panel-1').offset().top
      }, 500);

    ReactGA.event({
      category: this.pageName || "Unknown",
      action: 'Click',
      label: 'Scroll down from panel 0'
    });
  }

  scrolledToPanel(panelNumber, isVisible) {
    let sendEvent = false;
    switch (panelNumber){
      case 1:
        if(!panel1Seen && isVisible){
          panel1Seen = sendEvent = true;
          console.log('1')
        }
      
      break;
      case 2:
        if(!panel2Seen && isVisible){
          panel2Seen = sendEvent =  true;
                    console.log('2')

        }
      break;
      case 3:
        if(!panel3Seen && isVisible){
          panel3Seen = sendEvent =  true;
                    console.log('3')

        }
      break;
    }
    if(sendEvent){
      console.log(this.pageName)
      ReactGA.event({
        category: this.pageName || "Unknown",
        action: 'Scrolling',
        label: 'Panel ' + panelNumber
      });
    }

  }


  
}

export default PageComponent;



