'use strict';

import React from 'react';


import './landing-content.less';
import {Link} from 'react-router';



class LandingContent extends React.Component {
  render() {
    return (
      <div className="landing-content panels-container">
          <div className="panel panel-0">
            <video className="flying-background" autoPlay muted loop id="bgvid">
              <source src="/images/flying-over-madison-3.mp4" type="video/mp4"/>
            </video>


            <div className="madison-night-background">
            </div>
            
            <div className="vertically-centered-container">
              
              
              <div className="vertically-centered-content">
                <div className="logo-container">
                  <img src="/images/da-logo-white.svg"/>
                </div>
                <br/>
                <h2 className="description-content"> 
                  DarkAero designs and manufactures aerospace composite products and aircraft.
                  <br/>
                  <br/>
                  Our mission is to build a better kit aircraft. 
                </h2>
                <div className="mobile-buttons-container">
                  <button className="uppercase"><Link to='/composite-parts'>Composite Products</Link></button>
                  <button className="uppercase"><Link to='/kit-aircraft'>Aircraft</Link></button>
                </div>


              </div>
            </div> 
          </div>
          <div className="bottom-buttons-container">
            <div className="buttons-holder">
              <div className="contact-us" ><a href="mailto:info@darkaero.com">Contact Us</a></div>
              <div className="social-media"><a href="https://www.instagram.com/darkaeroinc/" target="_blank">Follow Us</a></div>
            </div>
          </div>
      </div>
    );
  }
}

LandingContent.displayName = 'LandingContent component';

// Uncomment properties you need
// LandingContent.propTypes = {};
// LandingContent.defaultProps = {};

export default LandingContent;



