'use strict';

import PageComponent from '../../page-component/PageComponent'
import VisibilitySensor  from 'react-visibility-sensor';

import MailingList from '../../mailing-list-component/MailingListComponent'

import React from 'react';


import './products-content.less';
import ReactGA from 'react-ga';


let panel1Seen = false;
let panel2Seen = false;
let panel3Seen = false;

class ProductsContent extends PageComponent { //
  constructor(props){
    super(props);
    this.orderPanel = this.orderPanel.bind(this);
    this.pageName = "Products Page"
  }
  orderPanel(panelChoice){
    ReactGA.event({
      category: this.pageName,
      action: 'Click',
      label: 'Buy Panel ' + panelChoice
    });
  }
  render() {
    return (
      <div className="products-content panels-container">
          <div className="panel panel-0" onClick={this.slideDown}>
            <div className="caliper-container">
            </div>
            
            <div className="vertically-centered-container">
              <div className="vertically-centered-content">
                <div className="large-title">Carbon Fiber + <span className="da-red">Honeycomb</span></div>
              </div>
            </div>
            <div className="down-arrow-container">
              <img className="da has-transition" src="/images/down-arrow.svg"/>
            </div> 
          </div>
          
          <div className="panel panel-1">
            <div className="vertically-centered-container panel-shade">
              <div className="vertically-centered-content">
                <div className="content-container composite-panel-text">                      
                  Carbon fiber and aluminum honeycomb panels are one of the lightest and strongest structural building 
                  blocks known to man. They are found in Formula One cars, supersonic aircraft, and orbital class rockets.
                  <br/>
                  <br/>
                  When designing a machine that challenges new frontiers, composite panels are an obvious choice, but 
                  their commercial availability is limited and their cost is restrictive. We had a need for materials 
                  that could meet the demands of our own mission.  Quality, strength, weight, and cost were critical, 
                  but no composite panel on the market could survive our testing, so we built our own.
                </div>
              </div>
            </div>
            <VisibilitySensor onChange={(isVisible)=> this.scrolledToPanel(1, isVisible)} />
 
          </div>
          <div className="panel panel-2">
            <div className="panel-shade">
              <div className="side-content-container">
                <div className="side-content description">
                  <div className="vertically-centered-container">
                    <div className="vertically-centered-content">
                     
                      <div className="content-container specs-description">
                        
                      DarkAero panels are engineered from the ground up with aerospace grade materials and made in the USA. 
                      They offer a superior CNC machined edge finish on both the carbon skin and the honeycomb core. 
                      Our panels are engineered to withstand temperatures up to 300 F, and are designed 
                      to survive continuous chemical exposure. Beyond this the surface finish is honest, free of any 
                      superficial coating that would add unnecessary weight and cost.
                      <br/>
                      <br/>
                      If you too have a mission that demands materials with high strength, low weight, and exceptional quality, our panels are 
                      suited for your needs. <a target="blank" href="/datasheets/Panel Datasheet - Aluminum HC, CF Skin, Gloss.pdf">Download our datasheet</a> or <a href="mailto:info@darkaero.com">contact us</a> for more details on their capabilities.
                  
                        
                        
                      </div>
                      
                    </div>
                  </div>
                </div>
                
                <div className="side-content">
                  <div className="vertically-centered-container">
                    <div className="vertically-centered-content">
                      
                       <img className="corner-panel" src="/images/production-panel.jpg"/>
                    </div>
                  </div>
                </div>
              </div>
              <VisibilitySensor onChange={(isVisible)=> this.scrolledToPanel(2, isVisible)} />
            </div>
  
          </div>
          <div className="panel panel-3">
            <div className="vertically-centered-container">
              <div className="vertically-centered-content">
                
                <div className="content-container">
                  
                  <h1>Availability</h1>
                  <div>Our carbon fiber and aluminum honeycomb panels are available in the following dimensional options. <a target="blank" href="/datasheets/Panel Datasheet - Aluminum HC, CF Skin, Gloss.pdf">Detailed specs can be found here.</a></div>
                  <br/>
                  <div className="panel-options-container">
                    <div className="panel-option-1 disabled" onClick={() => this.orderPanel(1)}>
                      <div className="panel-outline da has-transition">
                          <div className="vertically-centered-container">
                            <div className="vertically-centered-content">
                              1' x 1'
                            </div>
                          </div>
                      </div>
                      <br/>
                      <div>$69</div>
                      <br/>
                      <button disabled>Available Soon</button>
                    </div>
                    <div className="panel-option-2 disabled" onClick={() => this.orderPanel(2)}>
                      

                      <div className="panel-outline da has-transition">
                          <div className="vertically-centered-container">
                            <div className="vertically-centered-content">
                              2' x 2'
                            </div>
                          </div>
                      </div>
                      <br/>
                      <div>$249</div>
                      <br/>
                      <button disabled>Available Soon</button>
                    </div>

                    <div className="panel-option-3 disabled" onClick={() => this.orderPanel(3)}>
                      <div className="panel-outline">
                          <div className="vertically-centered-container">
                            <div className="vertically-centered-content">
                              2' x 4'
                            </div>
                          </div>
                      </div>
                      <br/>
                      <div>$449</div>
                      <br/>
                      <button disabled>Available Soon</button>
                    </div>

                    

                  </div>
                  <br/>
                  <br/>
                  <div>To receive updates on panel availability, subscribe to our panel mailing list below:</div>
                  <br/>
                  <MailingList mailingListDB='panels-mailing-list'/>
                  
                  
                  
                  
                </div>
              </div>
            </div>
            <VisibilitySensor onChange={(isVisible)=> this.scrolledToPanel(3, isVisible)} />
          </div>

            
      </div>
    );
  }
}


// Uncomment properties you need
// LandingContent.propTypes = {};
// LandingContent.defaultProps = {};

export default ProductsContent;



