'use strict';

import React from 'react';
import * as firebase from 'firebase';
import './mailing-list-component.less'

class MailingListComponent extends React.Component {
  
  constructor(props){
    super(props);

    this.state = {
      userEmail: "",
      emailSubmitted: false,
      emailSubmitError: false
    }
    
    this.emailChanged = this.emailChanged.bind(this);
    this.submitEmail = this.submitEmail.bind(this);
    this.handleKeyPress = this.handleKeyPress.bind(this);
  }


  emailChanged(event) {
    let userEmail = event.target.value;

    this.state.emailSubmitError = false;

    this.setState({
      userEmail: userEmail,
      emailSubmitError: false
    })
  }
  handleKeyPress(event){
    if(event.key === 'Enter'){
      this.submitEmail();
    }
  }
  submitEmail() {
    if(!this.state.userEmail) return;

    let validMailingListDB = ['mailing-list', 'panels-mailing-list'];
    let mailingListDB = validMailingListDB.indexOf(this.props.mailingListDB) >= 0 ? this.props.mailingListDB : 'mailing-list';

    let newEmailKey = firebase.database().ref().child(mailingListDB).push().key;

    let emailData = {}
    let self = this;
    console.log('submitting...')

    emailData['/' + mailingListDB + '/' + newEmailKey] = {
      email: this.state.userEmail
    }

    this.state.emailSubmitError = false;

    firebase.database().ref().update(emailData)
      .then(function(){
        self.setState({emailSubmitted: true});
      })
      ['catch'](function(error){
        
        self.setState({
          emailSubmitError: true
        })

      });


    

  }
  render() {
    return (
      <div className="mailing-list-component">
        <div className="email-input-container">
          <div className={(!this.state.emailSubmitted ? 'hidden ' : '') + 'subscribe-message da has-transition'}>
            <div className="vertically-centered-container">
              <div className="vertically-centered-content">
                Thanks! We'll keep you posted!
              </div>
            </div>
          </div>
          <input className={(this.state.emailSubmitted ? 'hidden ' : '') + 'da has-transition'} 
            placeholder="Type Email Here" 
            type="text" 
            value={this.state.userEmail} 
            onChange={this.emailChanged}
            onKeyPress={this.handleKeyPress}

          
          />
        </div>
        <div className={(!this.state.emailSubmitError ? 'hidden ' : '') + 'da has-transition da-red email-error'}>
          Your email could not be submitted. Please check it for errors. 
        </div>
        <button className={(this.state.emailSubmitted ? 'hidden ' : '') + 'da has-transition'} onClick={this.submitEmail}>Subscribe</button>
      </div>
    );
  }
}

MailingListComponent.displayName = 'MailingListComponent';

// Uncomment properties you need
// MailingListComponent.propTypes = {};
// MailingListComponent.defaultProps = {};

export default MailingListComponent;



