'use strict';

import React from 'react';
import PageComponent from '../../page-component/PageComponent';
import MailingList from '../../mailing-list-component/MailingListComponent';
import VisibilitySensor  from 'react-visibility-sensor';


import * as firebase from 'firebase';

import './aircraft-content.less';


class AircraftContent extends PageComponent {
  constructor(props){
    super(props);

    this.pageName = "Aircraft Page";
  }

  

  render() {
    return (
      <div className="aircraft-content panels-container">
          <div className="panel panel-0" onClick={this.slideDown}>
            <video autoPlay muted loop id="bgvid" class="hangar-door-moving">
              <source src="/images/hangar-door-opening.mp4" type="video/mp4"/>
            </video>
            <div className="hangar-door-static-background">
            </div>
            
            
            <div className="vertically-centered-container">
              
              
              <div className="vertically-centered-content">
                <div className="large-title">DarkAero <span className="da-red">1</span></div>
              </div>
            </div>
            <div className="down-arrow-container">
              <img className="da has-transition" src="/images/down-arrow.svg"/>
            </div>  
          </div>
          <div className="panel panel-1">
            <div className="vertically-centered-container">
              <div className="vertically-centered-content">
                <div className="content-container">
                  Every aircraft is designed for a mission. We wanted speed, range and efficiency, but
                  no aircraft in existence had the capability to meet our needs. 
                  It wasn't enough to simply improve on an existing design. We had to start over.
            
                  
                  
                </div>
              </div>
            </div>
            <VisibilitySensor onChange={(isVisible)=> this.scrolledToPanel(1, isVisible)} />
          </div>
          <div className="panel panel-2">
            <div className="vertically-centered-container">
              <div className="vertically-centered-content">
                <div className="content-container">
                  We set out to create a better aircraft. We selected the most advanced engine and 
                  designed an airframe around it. Using precision tools, we're building every part from 
                  the lightest and strongest materials.
                </div>
              </div>
            </div>
            <VisibilitySensor onChange={(isVisible)=> this.scrolledToPanel(1, isVisible)} />
          </div>
          
          <div className="panel panel-3">
            <div className="vertically-centered-container">
              <div className="vertically-centered-content">
                <div className="content-container">
                  
                  We're testing and refining every detail to squeeze every possible ounce of performance
                  into our design. We're calling it the DarkAero 1.
                  <br/>
                  <br/>
                  To follow our progress, subscribe to our mailing list below or follow us on social media.
                  <br/>
                  <br/>
                  <div className="sign-up-container">
                    <MailingList mailingListDB='mailing-list'/>
                  </div>
                </div>
              </div>
            </div>
            <VisibilitySensor onChange={(isVisible)=> this.scrolledToPanel(2, isVisible)} />
          </div>
          
      </div>
    );
  }
}

AircraftContent.displayName = 'AircraftContent component';

// Uncomment properties you need
// AircraftContent.propTypes = {};
// AircraftContent.defaultProps = {};

export default AircraftContent;



