require('normalize.css/normalize.css');


import React from 'react';
import 'styles/App.less';
import Navbar from 'components/navbar/Navbar.js';
import ProductsContent from 'components/views/products-content/ProductsContent.js'

import LandingContent from 'components/views/landing-content/LandingContent.js';
import AircraftContent from 'components/views/aircraft-content/AircraftContent.js';
import ReactGA from 'react-ga';

import { Router, Route, Link, IndexRoute, hashHistory, browserHistory } from 'react-router';

import * as firebase from 'firebase';


//initialize react
var config = {
  apiKey: "AIzaSyBg4zJIyWvJ8HCPFDorNwHSnXeUfvU0iTc",
  authDomain: "darkaero-5a308.firebaseapp.com",
  databaseURL: "https://darkaero-5a308.firebaseio.com",
  storageBucket: "darkaero-5a308.appspot.com",
  messagingSenderId: "266250879376"
};
firebase.initializeApp(config);

//initialize google analytics
ReactGA.initialize('UA-93337511-1');

let logPageView = function(){
  ReactGA.set({ page: window.location.pathname });
  ReactGA.pageview(window.location.pathname);
}

const Container = (props) => <div className='app-container'>
  <Navbar/>
  {props.children}
</div>

class AppComponent extends React.Component {
  
  render() {
    return (
      <Router history={browserHistory} onUpdate={logPageView}>
          <Route  path='/' component={Container}>
            <IndexRoute component={LandingContent} />
            
            <Route path='/composite-parts' component={ProductsContent} />
            <Route path='/kit-aircraft' component={AircraftContent} />
            <Route path='*' component={LandingContent} />
          </Route>
      </Router>
    );
  }
}

AppComponent.defaultProps = {
};

window.onhashchange = function() { 
     //code 
     console.log('asdfdas') 
     menuChecked = false;
}

export default AppComponent;



